.OutputDisplay {
    background-color: #fff;
    border: 1px solid #d4dad1;
    border-radius: 4px;
    -ms-flex: 1 1 0%;
        flex: 1 1 0%;
    margin: 16px auto;
    max-width: 722px;
    min-height: 120px;
    overflow: auto;
    padding: 2px;
    text-align: left;
    width: calc(100% - 30px);
}
@media screen and (min-width: 425px) {
    .OutputDisplay {
        padding: 4px;
    }
}
@media screen and (min-width: 768px) {
    .OutputDisplay {
        padding: 8px;
    }
}
@media screen and (min-width: 1024px) {
    .OutputDisplay {
        margin: 64px auto;
    }
}

.OutputDisplay__content {
    position: relative;
    white-space: pre-wrap;
}

.OutputDisplay__content:after {
    -webkit-animation: flashcursor .9s infinite;
            animation: flashcursor .9s infinite;
    background-color: #666;
    content: "";
    position: absolute;
    height: 20px;
    width: 1px;
}

@-webkit-keyframes flashcursor {
    0% { opacity: 0; }
    49.9% { opacity: 0; }
    50% { opacity: 1; }
    99.9% { opacity: 1; }
}

@keyframes flashcursor {
    0% { opacity: 0; }
    49.9% { opacity: 0; }
    50% { opacity: 1; }
    99.9% { opacity: 1; }
}



